
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

* { -webkit-box-sizing: border-box; box-sizing: border-box
}
ul {margin : 0 ; padding : 0
}
ul .li {
      cursor: pointer ; position : relative;padding : 8px 8px 8px 40px;
      background:#eee;font-size:14px; -webkit-transition : 0.2s; transition : 0.2s;
      -webkit-user-select: none;-moz-user-select: none;
      -ms-user-select: none; user-select: none;
}
#todolist{
  list-style:none;
  height:25vw;
  overflow-y:scroll;
}
.li{
  font-family:'Nanum-Gothic';
  font-weight:bold;
  border:1px solid;
  background-color:white !important ;
  margin-bottom:2px;
  padding:10px 0px !important;
  border-radius : 5px;
}
ul .li:hover {background:#eee
}
ul .li.checked{background:#ddd;-webkit-text-decoration: line-through red;text-decoration: line-through red;
}
.close{
      position: absolute ; right: 0 ; top: 0;
      color:#F39C12;
}
.close:hover{
      color: red
}
.header{
      background-color: white ; padding: 30px 0px;
      color : black; text-align: center;
      width:100%;
}
.header:after{
      content:"";display: table; clear:both;
}
.input{
      border:none ; width: 80%; height: 35px; padding:10px;
      float: left; font-size:16px;
      border-radius:5px
}
.addbutton{
      padding:10px; width:19% ; height: 35px ; background : #2D3E50;
      color:white;
      float : left; text-align: center;
      font-size: 13px; cursor: pointer; -webkit-transition: 0.3s; transition: 0.3s;
      font-weight: bold;
      font-family: 'Nanum Gothic';
      border-radius:5px;
      margin-left:1px;
}
.addbutton:hover{background-color:#F39C12
}
.completed {text-decoration: none
}
.input{outline:none;border:1px #2D3E50 solid;border-radius:10px;
}
